@import url("https://fonts.googleapis.com/css2?family=Public+Sans:wght@100;200;300;400;500;600;700;800&display=swap");

* {
  font-family: "Public Sans", sans-serif;
}
#productList,
#productDetails {
  margin: 2rem;
}

.heading {
  font-size: 24px;
  color: #212b36;
  margin-bottom: 3rem;
}

.table_headings {
  font-style: normal !important;
  color: #637381 !important;
  font-weight: 600 !important;
  font-size: 13px !important;
}

a:link {
  text-decoration: none;
  color: #212b36;
}

.info_cont {
  display: flex;
  justify-content: space-around;
}

.img_container {
  width: 45%;
  display: flex;
  justify-content: center;
  aspect-ratio: 3/3.5;
  box-sizing: border-box;
  padding: 2.5rem 0;
}

.img_container img {
  width: 100%;
  height: 100%;
}

.details {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-top: 3rem;
  width: 30%;
}

.sale {
  text-align: center;
  border-radius: 6px;
  width: 48px;
  height: 20px;
  padding: 2px;
  background-color: #ff5630;
  color: white;
}

.descBox {
  margin-top: 2rem;
  background: #ffffff;
  padding: 10px 24px;
  box-shadow: 0px 0px 2px rgba(145, 158, 171, 0.2),
    0px 12px 24px -4px rgba(145, 158, 171, 0.12);
  border-radius: 16px;
}

.box1 {
  margin-bottom: 2rem;
}

.sizeChart {
  text-align: end;
  font-family: "Public Sans";
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  line-height: 18px;
  text-decoration-line: underline;
}

.title {
  color: #212b36;
  font-weight: 400;
}

.b1,
.b2,
.minus,
.add {
  cursor: pointer;
}

.counter {
  display: flex;
  align-items: center;
  justify-content: space-around;
  border-radius: 8px;
  height: 24px;
  width: 100px;
  padding: 5px;
  border: 1px solid #919eab;
  background-color: #ffffff;
}

.title-col a:link {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.category {
  color: #00b8d9;
  text-transform: capitalize;
  font-weight: 700;
  font-size: 12px;
}

.prod_name {
  color: #212b36;
  font-weight: 700;
  font-size: 20px;
}
.price {
  color: #212b36;
  font-weight: 700;
  font-size: 24px;
}
.rating {
  color: #637381;
  font-size: 14px;
}

.size {
  display: flex;
  justify-content: space-between;
}

.quantity {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.size label,
.quantity p {
  color: #212b36;
  font-weight: 600;
  font-size: 14px;
  cursor: pointer;
}

button {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 11px 22px;
  gap: 8px;
  width: 182px;
  height: 48px;
  border-radius: 8px;
  border: none;
}

.adc {
  margin-left: 10px;
}

.title {
  font-weight: 700;
  font-size: 14px;
}

.desc {
  font-weight: 400;
  font-size: 12px;
}

.box3 {
  justify-content: space-between;
  display: flex;
}

.b1 {
  font-weight: 700;
  font-size: 15px;
  background-color: #ffab00;
  color: #212b36;
}

.b2 {
  font-weight: 700;
  font-size: 15px;
  color: white;
  background-color: #00ab55;
}

.line {
  border: none;
  border-top: 2px dotted #919eab;
  opacity: 20%;
}

select {
  box-sizing: border-box;
  width: 150px;
  padding: 2px;
  height: 36px;
  border: none;
  border: 1px solid #637381;
  border-radius: 8px;
}

.count {
  text-align: end;
  color: #637381;
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
}

.box2 {
  margin: 2rem 0;
}

.box3 {
  margin: 2rem 0;
}
.minus,
.plus {
  padding-top: 2px;
}

.b2:hover {
  background-color: #4bf19e;
  color: #212b36;
}
.b1:hover {
  background-color: #f7cd78;
}

@media screen and (max-width: 1024px) {
  .info_cont {
    margin: 0 auto;
    width: 100%;
    justify-content: center;
  }
  .heading {
    margin-bottom: 1rem;
  }
  .img_container {
    aspect-ratio: 1/1;
  }
  .img_container img {
    width: 100%;
    height: 100%;
    aspect-ratio: 1/1;
  }

  .box1,
  .box2,
  .box3 {
    margin: 1rem 0;
  }
  .details {
    margin-left: 10%;
    width: 45%;
  }
}

@media screen and (max-width: 768px) {
  .details {
    text-align: center;
    padding: 0;
  }
  .info_cont {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .img_container {
    width: 70%;
    aspect-ratio: 1/1;
  }
  .details {
    width: 70%;
    margin: 0;
  }
}

@media screen and (max-width: 605px) {
  .box3 {
    flex-direction: column;
  }
  button {
    width: 100%;
    margin-bottom: 1rem;
  }
}

@media screen and (max-width: 425px) {
  #productList {
    margin: 0.5rem;
  }
  #productDetails {
    margin: 1rem;
  }
  .img_container {
    width: 100%;
  }
  .details {
    padding: 0;
    width: 100%;
  }
}
